import { NavLink } from "react-router-dom";
// import classes from './header.module.css'
import classes from '../layout/MainNavigation.module.css'

const MainNavigation = () => {


    return (
        <header className={classes.header}>
            <div className={classes.logo}>
                <p>Your Quote</p>
            </div>
            <nav className={classes.nav}>
                <ul>
                    <li><NavLink to='/quotes' activeClassName={classes.active}>All Quote</NavLink></li>
                    <li><NavLink to='/new-quote' activeClassName={classes.active}>Add a Quote</NavLink></li>
                </ul>
            </nav>

        </header>
    )
}

export default MainNavigation;